import React,{useState} from 'react';
import {NavLink} from "react-router-dom";
import OopDB from './OopDB';
import Oops_box from "./Oops_box";
import Pagination from "./Pagination";

const OOPs = () => {

  const [showPerPage ] = useState(3);
  const [pagination, setPagination]=useState({
    start:0,
    end:showPerPage,
  })

  const onPaginationChange = (start, end)=>{
   setPagination({start:start,end:end});
  }

    return (<>
      <div className="cpp-container container">
        <h1 className="Sub-title text-center pt-3">C++ Programming Language</h1><hr/>
        <div className="pb-2">
        <div id="wrapper">
            <div id="sidebar-wrapper" style={{height:"25%"}}>
              <ul className="sidebar-nav">
              <h2 className="topic-title">Topics</h2><hr width="80%"/> 
                <li><NavLink to="/B.Sc.IT/cpp" className="topic-link ">Introduction</NavLink></li><hr width="80%"/> 
                <li><NavLink to="/B.Sc.IT/cpp/oop" className="topic-link topic-active">OOPs</NavLink></li><hr width="80%"/>
              </ul>
            </div>
            <div id="page-content-wrapper">
                <div className="page-content">
                  <div className="container">

                     {
                       OopDB.slice(pagination.start, pagination.end).map((val,ind)=>{
                         return <Oops_box key={ind} discrip={val.detail}/>
                       })
                     }
                </div>
              </div>
            </div>
        </div>
      </div>
    </div><br/>
    <div className="container">
      <Pagination key="1" showPerPage={showPerPage} 
      onPaginationChange={onPaginationChange}
      total={OopDB.length}/> 
      </div>
    <br/>
    </>
  );
};

export default OOPs;