import React,{useState} from 'react';
import AndroidAssignData from './AndroidAssignData';
import {NavLink} from "react-router-dom";
import Pagination from "./Pagination";
const AndroidAssign = () =>{
  const [showPerPage ] = useState(1);
  const [pagination, setPagination]=useState({
    start:0,
    end:showPerPage,
  })
  const onPaginationChange = (start, end)=>{
   setPagination({start:start,end:end});
  }

     return(
         <>
<div className="main-container container">
         <h1 className="Sub-title text-center pt-3">Android</h1><hr/>
            
            
         <div id="wrapper">
            <div id="sidebar-wrapper" style={{height:"25%"}}>
              <ul className="sidebar-nav">
              <h2 className="topic-title">Topics</h2> <hr width="80%"/>
                <li><NavLink to="/B.Sc.IT/android/" className="topic-link ">Introduction</NavLink></li><hr width="80%"/> 
                <li><NavLink to="/B.Sc.IT/android/pgm1" className="topic-link">Build your first app</NavLink></li><hr width="80%"/>
                <li><NavLink to="/B.Sc.IT/android/assign" className="topic-link topic-active">Assignments</NavLink></li><hr width="80%"/>
              </ul>
            </div>
            <div id="page-content-wrapper">
                <div className="page-content border-bottom border-right border-dark">
                {
                AndroidAssignData.slice(pagination.start, pagination.end).map((val,ind)=>{
                return <div id={ind} className="container">{val.assignmente}</div>
     
                })}
              </div>
            </div>
        </div>
   </div>
   <div className="container mt-2">
                <Pagination key="1" showPerPage={showPerPage} 
            onPaginationChange={onPaginationChange}
            total={AndroidAssignData.length}/> 
            </div><br/><br/>
         </>
     );
}
export default AndroidAssign;