const MainActivity2 = `package com.example.arithmatic;

import androidx.appcompat.app.AppCompatActivity;

import android.os.Bundle;
import android.view.View;
import android.widget.Button;
import android.widget.EditText;
import android.widget.TextView;

public class MainActivity extends AppCompatActivity {

	// define the global variable 
	// variable number1, number2 for input input number and op for operator input
	// equls, res for result textView 

    EditText num1;
    EditText num2;
    EditText op;
    Button equls;
    TextView res;
    @Override
    protected void onCreate(Bundle savedInstanceState) {
        super.onCreate(savedInstanceState);
		setContentView(R.layout.activity_main);
		
		// by ID we can use each component which id is assign in xml file
	num1 = (EditText)findViewById(R.id.e1);
        num2 = (EditText)findViewById(R.id.e2);
        op = (EditText)findViewById(R.id.e3);
        equls = (Button)findViewById(R.id.btn);
        res = (TextView)findViewById(R.id.result);
 
		// Add_button add clicklistener 
        equls.setOnClickListener(new View.OnClickListener() {
            @Override
            public void onClick(View v) {
				
				// num1 or num2 double type 
				// get data which is in edittext, convert it to string 
				// using parse Double convert it to Double type 
		double num1 = Double.parseDouble(num1.getText().toString());
                double num2 = Double.parseDouble(num2.getText().toString());
                double sum = 0;
                String d;
                String c = op.getText().toString();
                
                switch (c)
                {
                    case "+" : sum = a+b;res.setText(Double.toString(sum)); break;
                    case "-" : sum = a-b;res.setText(Double.toString(sum)); break;
                    case "*" : sum = a*b;res.setText(Double.toString(sum)); break;
                    case "/" : sum = a/b;res.setText(Double.toString(sum)); break;
                    default: d = "error";
                             res.setText(d);
                }
            }
        });
    }
}
`;

